import { Component, OnInit } from '@angular/core';
import { Geolocation } from '@ionic-native/geolocation/ngx';
import { Service } from 'src/app/services/Service';
import { Legenda } from 'src/app/entities/legenda';
import { CommonfunctionService } from 'src/app/services/commonfunction.service';
import { Settings } from 'src/app/entities/settings';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';

interface marker {
    lat: number;
    lng: number;
    label?: string;
    draggable: boolean;
    iconUrl?:  {
        url?: string,
        labelOrigin?: {x: number, y: number, equals},
        scaledSize?: {height: number, width: number, equals}
    };
    iconNameHtml: string,
    iconImgUrlHtml: string,
    labelOptions?: {
        color: string,
        fontFamily: string,
        fontSize: string,
        fontWeight: string,
        text: string
    };
}

@Component({
  selector: 'app-mapa',
  templateUrl: './mapa.page.html',
  styleUrls: ['./mapa.page.scss']
})

export class MapaPage implements OnInit {
  legendy: Legenda[] = [];
  loadingLegenda = true;
  lastNumberLegenda = 20;
  pageLegenda = 1;
  searchLegenda: string = '';
  
  mapLat: number = 53.62768894448802;
  mapLng: number = 21.808961338541817;
  myLat: number;
  myLng: number;
  zoom: number = 9;
  public origin: any;
  public destination: any;
  loadingMoreData = false;
  currentZoom = 11;
  showSelected = false;
  loading = true;
  positions: marker[] = [];
  openedWindow : number = 0;
  currentIW: any;
  previousIW: any;
  equals;
  locationActive: boolean = false;
  locationLoading: boolean = false;
  locateDot: any = false;
  clickedLocate: number = 0;
  minClusterSize = 50;

  map: any;
  address: string;
  mapStyle: any;

   
  clusterMarker = [
    {
      url: "../../assets/imgs/cluster.svg",
      width: 60,
      height: 60,
      rect: 30,
      textColor: "var(--ion-color-gold)",
      textSize: 30,
    },
    {
      url: "../../assets/imgs/cluster.svg",
      width: 60,
      height: 60,
      rect: 30,
      textColor: "var(--ion-color-gold)",
      textSize: 30,
    }
  ];

  clusterCalculator = {
    calculate: (markers) => {
      for (let i = 0; i < markers.length; i++) {
        console.log(i)
        if (i = 2){
          return { text: markers.length, index: 1 };  
        }
        if (i = 3){
          return { text: markers.length, index: 2 };  
        }
      }
    }
  };


constructor(
  public service: Service,
  public geolocation: Geolocation,
  public CFS: CommonfunctionService,
  public settings: Settings,
  public route: ActivatedRoute,
  public translate: TranslateService,
) {
    this.currentIW = null;
    this.previousIW = null;
    this.settings.langCode = this.route.snapshot.paramMap.get("lang");
    document.documentElement.setAttribute("lang", this.settings.langCode);
}
    
  loadMoreLegendy(lang: string) {
    this.loadingLegenda = true;
    this.service.getLegendaMapAll(lang).subscribe((data: Legenda[]) => {
      if (data && data.length > 0) {
        // console.log(data)
        this.legendy = this.legendy.concat(data);
        this.lastNumberLegenda = data.length;
        this.pageLegenda = this.pageLegenda + 1;
        this.loadingLegenda = false;
        this.legendy.forEach(Legenda => {
          if ( this.settings.isPro === true ) {
            Legenda.ACF.wersja_pro = false;
          }
        });
      } else {
        this.lastNumberLegenda = 0;
        this.loadingLegenda = false;
      }
    });
  }

  appToast() {
    this.CFS.presentToast( this.translate.instant('APP_SOON'),'top',1300 );
  }
  
  ngOnInit() {
  }
  
  myPosition() {
    this.clickedLocate = this.clickedLocate + 1;
      this.locationLoading = true;
      this.geolocation.getCurrentPosition().then((resp) => {
        this.myLat = resp.coords.latitude;
        this.myLng = resp.coords.longitude;
        this.mapLat = resp.coords.latitude;
        this.mapLng = resp.coords.longitude;
        this.zoom = 9;
        this.locationActive = true;
        this.locationLoading = false;
        this.CFS.presentToast(this.translate.instant('YOUR-CO-ORDINATES') + this.myLat + ', ' + this.myLng,'top',3000);
      }).catch((error) => {
        console.log(error);
        this.locationActive = false;
        this.locationLoading = false;
        if (error.code == 1) {
          this.CFS.presentToast( this.translate.instant('UNLOCK_LOCALIZATION'),'top',3000 );
        } else if (error.code == 2) {
          this.CFS.presentToast( this.translate.instant('CANT_GET_LOCALIZATION'),'top',3000 );
        } else if (error.code == 3) {
          this.CFS.presentToast( this.translate.instant('LIMIT_LOCALIZATION'),'top',3000 );
        }
      });
  }

  checkPro(item: boolean) {
    if (item === true) {
      return '../../../assets/imgs/marker-pro.svg';
    } else {
      return '../../../assets/imgs/marker.svg';
    }
  }
  
  ionViewDidEnter() {
  }

  openWindow(id) {
    this.openedWindow = id;
  }

  isInfoWindowOpen(id) {
    return this.openedWindow == id;
  }

  mapClick() {
    if (this.previousIW) {
      this.previousIW.close();
    }
  }

  markerClick(infoWindow) {
    if (this.previousIW) {
      this.currentIW = infoWindow;
      this.previousIW.close();
    }
    this.previousIW = infoWindow;
  }

  centerChanged(event:any){
    this.myLat;
    this.myLng;
    this.mapLat;
    this.mapLng;
    this.zoom = 10;
  }


}